import React, { useRef, useState } from 'react'; 
import { Button, styled } from '@mui/material'; 
import { capture, OutputType } from 'html-screen-capture-js';


const StartButton = styled(Button)({ 

});

const Destroyer = () => {

    const videoRef = useRef<HTMLVideoElement>(null);
    const [screenshot, setScreenshot] = useState<string | null>(null);

    const captureScreen = async () => {
        try {
            const stream = await navigator.mediaDevices.getDisplayMedia({ video: true });
            
            if (videoRef.current) {
                videoRef.current.srcObject = stream;

                videoRef.current.onloadedmetadata = () => {
                    if (videoRef.current) {
                        // Create a canvas element to draw the video frame
                        const canvas = document.createElement('canvas');
                        canvas.width = videoRef.current.videoWidth;
                        canvas.height = videoRef.current.videoHeight;

                        // Draw the video frame on the canvas
                        const ctx = canvas.getContext('2d');
                        if (ctx) {
                            ctx.drawImage(videoRef.current, 0, 0, canvas.width, canvas.height);

                            // Convert the canvas content to a data URL (base64-encoded image)
                            const dataUrl = canvas.toDataURL('image/png');

                            // Set the screenshot state
                            setScreenshot(dataUrl);

                            // Stop the video stream
                            stream.getTracks().forEach(track => track.stop());
                        }
                    }
                };
            }
        } catch (error) {
            console.error('Error capturing screen:', error);
        }
    };

    const startDestroyer = () => {
        const scr = capture(OutputType.STRING,
                            window.document,{
                                cssSelectorsOfIgnoredElements: [],
                                rulesToAddToDocStyle: [],
                                imageFormatForDataUrl: "image/jpeg"
                              })  

        console.log(scr)
    }
    
    const handleScreenCapture = (screenCapture:any) => {
        setScreenshot(screenCapture);
    } 

    return (
        < >
           
            <StartButton 
                onClick={startDestroyer}  
                color="inherit" 
                sx={{ flexGrow: 2 }}>
                Start
            </StartButton>
           
            {screenshot && <img src={screenshot} alt="Screenshot" />}
            <video ref={videoRef} style={{ display: 'none' }} />
       </ >
    );
};

export default Destroyer;